html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;font-family:sans-serif}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}html,body,#root{height:100%}button{background-color:#edeff8;border:1px solid #cbcdd3;border-radius:.3em;color:#515664;cursor:pointer;display:inline-block;flex:1;font-size:1rem;justify-content:center;padding-bottom:calc(0.375em - 1px);padding-left:0.75em;padding-right:0.75em;padding-top:calc(0.375em - 1px);text-align:center;text-decoration:none;vertical-align:middle;white-space:nowrap}button:hover{background-color:#e1e1e3;border-color:#3d4a6b;color:#4f5a74}button.selected,button:active,button:focus{background-color:#d4d4d7;border-color:#3d4a6b;color:#4f5a74}button[disabled]{color:#a6a3a3;background-color:#ddd;border-color:#bfbfbf;cursor:default}h1,h2,h3{text-align:center;font-weight:lighter}h1{text-align:center;font-weight:lighter;color:#333;line-height:1.125;margin-bottom:1.5rem;font-size:3rem}h2{text-align:center;font-weight:lighter;color:#333;line-height:1.125;margin-bottom:1.25rem;font-size:1.6rem}p{margin-bottom:1em}aside{color:#b9b8b8;text-align:center}.breadcrumbs{position:absolute;padding:.5em;z-index:1}.breadcrumbs li{display:inline-block}.breadcrumbs a{display:inline-block;padding-right:.5em;text-decoration:none;color:#556386}.breadcrumbs a:hover{color:#858B9A}.tc_low{color:#030d99}.tc_mid{color:#a59400}.tc_high{color:#ad0000}.tc_none{color:#252525}.modal{background-color:rgba(0,0,0,0.4);bottom:0;display:none;left:0;position:fixed;right:0;top:0;z-index:3;justify-content:center;align-items:center}.modal.visible{display:flex}.modal .fg{background-color:white;border-radius:.4em;border:1px solid #b9b8b8;box-shadow:0.2em 0.2em 0.2em rgba(0,0,0,0.3);padding:2em;text-align:center;max-width:90%;width:20em;margin:0 1em}.modal .bg{bottom:0;left:0;position:absolute;right:0;top:0}.modal button{margin:1em .2em 0}.modal .danger{background-color:#c40404;color:white}
