.practice{padding:3rem 1.5rem}@media (max-width: 768px){.practice{font-size:.8rem}}.practice .test-button{background-color:#edeff8;border:1px solid #cbcdd3;border-radius:.3em;color:#515664;cursor:pointer;display:inline-block;flex:1;font-size:1rem;justify-content:center;padding-bottom:calc(0.375em - 1px);padding-left:0.75em;padding-right:0.75em;padding-top:calc(0.375em - 1px);text-align:center;text-decoration:none;vertical-align:middle;white-space:nowrap;position:absolute;right:1em;top:1em;z-index:1}.practice .test-button:hover{background-color:#e1e1e3;border-color:#3d4a6b;color:#4f5a74}.practice .test-button.selected,.practice .test-button:active,.practice .test-button:focus{background-color:#d4d4d7;border-color:#3d4a6b;color:#4f5a74}.practice .test-button[disabled]{color:#a6a3a3;background-color:#ddd;border-color:#bfbfbf;cursor:default}.practice .test-button .icon{display:inline-block;padding-left:.5em;margin-right:-.5em}.practice .preview{text-align:center !important}.practice .preview section{padding:3rem 1.5rem}.practice .preview .start-button{background-color:#00d1b2;border:1px solid #49c2a0;border-radius:.3em;color:#fff;cursor:pointer;display:inline-block;flex:1;font-size:1rem;justify-content:center;padding-bottom:calc(0.375em - 1px);padding-left:0.75em;padding-right:0.75em;padding-top:calc(0.375em - 1px);text-align:center;text-decoration:none;vertical-align:middle;white-space:nowrap;border-radius:.2em;font-size:1.5em;width:100%;padding-left:0;padding-right:0;width:50%;margin:0 1em 1em 1em}.practice .preview .start-button:hover{background-color:#00b89c;border-color:#47b797;color:#fff}.practice .preview .start-button.selected,.practice .preview .start-button:active,.practice .preview .start-button:focus{background-color:#009f86;border-color:#47b797;color:#fff}.practice .preview .start-button[disabled]{color:#a6a3a3;background-color:#ddd;border-color:#bfbfbf;cursor:default}.practice .cards{position:absolute;left:0;top:3em;width:100%}.practice .cards .card{width:50%;margin-left:25%;margin-top:15%}.practice .card{background-color:transparent;border-radius:.25em;cursor:pointer;font-size:4em;padding-top:.5em;position:absolute;text-align:center;transition:opacity .3s}.practice .card>div{transition:opacity .3s;opacity:0;padding-bottom:.5em}.practice .card>div.visible{opacity:1}.practice .card>div.pronunciation{font-size:.8em}.practice .card>div.term{font-size:.6em}.practice .card button{font-size:.4em;vertical-align:top;margin-top:.4em}.practice .card-exit{opacity:0}.practice .next-button{border:0.1em solid #e5e9f1;border-radius:1em;color:#b9b8b8;display:inline-block;font-size:.75em;height:1em;width:1em}@media (min-width: 768px){.practice .next-button{display:none}}.practice .help-button{background-color:#edeff8;border:1px solid #cbcdd3;border-radius:.3em;color:#515664;cursor:pointer;display:inline-block;flex:1;font-size:1rem;justify-content:center;padding-bottom:calc(0.375em - 1px);padding-left:0.75em;padding-right:0.75em;padding-top:calc(0.375em - 1px);text-align:center;text-decoration:none;vertical-align:middle;white-space:nowrap;background-color:#edeff8;border-radius:1em;border:1px solid #cbcdd3;color:#515664;display:inline-block;font-size:1.2rem;height:1.5em;line-height:1.5em;margin:0;padding:0;text-align:center;width:1.5em;cursor:pointer;position:absolute;z-index:1;display:none !important}.practice .help-button:hover{background-color:#e1e1e3;border-color:#3d4a6b;color:#4f5a74}.practice .help-button.selected,.practice .help-button:active,.practice .help-button:focus{background-color:#d4d4d7;border-color:#3d4a6b;color:#4f5a74}.practice .help-button[disabled]{color:#a6a3a3;background-color:#ddd;border-color:#bfbfbf;cursor:default}@media (max-width: 768px){.practice .hint{display:none}}.practice table button{margin-right:1em}
